<template>
  <div class="recent-updates">
    <div class="top">
      <div class="back-btn" @click="back">返回</div>
      <div class="laiyuan">
        <span>来源</span>
        <el-select v-model="source" placeholder="请选择" size="mini" @change="getlist">
          <el-option
            v-for="item in yuanlist"
            :key="item.value"
            :label="item.source=='shenqi'?'追书神器':'追书网'"
            :value="item.source">
          </el-option>
        </el-select>
      </div>
    </div>
    <table v-loading="loading">
      <thead >
        <td class="table-head num">序号</td>
        <td class="table-head book-name">书名</td>
        <td class="table-head author">作者</td>
        <td class="table-head source">来源</td>
        <td class="table-head update-time">更新时间</td>
        <td class="table-head latest-chapters">最新章节</td>
      </thead>
      <tr v-for="(item,index) in list" :key="index" @click="tiao(item.id)">
        <td class="num">{{index+1}}</td>
        <td class="book-name">
          <span>{{item.title}}</span>
        </td>
        <td class="author">{{item.author}}</td>
        <td class="source">{{item.source=="shenqi"?"追书神器":"追书网"}}</td>
        <td class="update-time">{{item.update_time}}</td>
        <td class="latest-chapters">{{item.recent_chapter}}</td>
      </tr>
    </table>
    <div class="fenye">
       <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNum+1"
        :page-sizes="[10, 20, 30, 40, 50, 100]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data(){
    return{
      list:[],
      pageNum:0,//页码
      pageSize:10,
      source:"",
      yuanlist:[],
      total:0,//总数
      loading:true
    }
  },
  methods:{
    back(){
      this.$router.back();
    },
    tiao(id){
      this.$router.push({path:`bookDetails?id=${id}`});
    },
    //获取最近更新
    getlist(){
      this.loading=true
      let url="http://zhuishu.zhiask.cn/api";
      let data={
         "action": "list_book",
          "data": {
              "page":this.pageNum,
              "num": this.pageSize,
              "source":this.source
          }
      }
      let token=sessionStorage.getItem("token")
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      console.log(data);
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,
         "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        this.loading=false;
        console.log(res.data.data);
        this.list=res.data.data;
        this.total=res.data.total;
        if(this.list.length==0){
          this.$message.error("暂无更新")
        }
        this.getyuan();
      })
    },
    //获取源
    getyuan(){
      let url="http://zhuishu.zhiask.cn/api";
      let data={
         "action": "list_crawler",
          "data": {
          }
      }
      let token=sessionStorage.getItem("token")
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      console.log(data);
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,
         "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        console.log(res.data.data);
        this.yuanlist=res.data.data;
      })
    },
    handleSizeChange(e){
      this.pageSize=e;
      this.getlist();
    },
    handleCurrentChange(e){
      this.pageNum=e-1;
      this.getlist();
    }
  },
  created(){
    this.getlist();
  }
}
</script>
<style>
  a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li,select{
  margin:0px;
  padding:0px;
}
.recent-updates{
  box-sizing:border-box;
  padding:0px 32px;
}
.recent-updates .top{
  width: 100%;
  padding: 14px 0px 15px 0px;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}
.recent-updates .top .back-btn{
  width: 80px;
  height: 30px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  font-size: 12px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  line-height: 30px;
  cursor: pointer;
}
.recent-updates .top .laiyuan select{
  width: 137px;
  height: 27px;
  line-height: 18px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  border-radius: 5px;
  outline: none;
}
.recent-updates table{
  width: 100%;
  height: 54px;
  text-align: center;
  border: 1px solid rgba(187, 187, 187, 1);
  margin-top:4px;
  border-collapse: separate;
  border-spacing: 0px 0px;
  font-size: 12px;
}
.recent-updates table td{
  height: 56px;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
  cursor: pointer;
}
.recent-updates table .table-head{
  height: 52px;
  background-color: rgba(247, 247, 247, 1);

}
.recent-updates .num{
  width: 5%;
}
.recent-updates .author{
  width: 10%;
}
.recent-updates .source{
  width: 13%;
}
.recent-updates .update-time{
  width: 18%;
}
.recent-updates .latest-chapters{
  width: 26%;
}
.recent-updates table .book-name{
  width:15%;
  text-align: left;
}
.recent-updates table .book-name .book-img{
  width: 33px;
  height: 33px;
  display: inline-block;
  vertical-align: middle;
}
.recent-updates table .book-name .book-img img{
  width: 100%;
  height: 100%;
}

.fenye{
  width:600px;
  display: flex;
  margin:50px auto;
}
</style>


