<template>
  <div class="login">
    <form action="">
      <div class="title">更改密码</div>
      <div class="name">
        <span>旧密码:</span>
        <input id="uname" type="text" autocomplete="off"  placeholder="请输入旧密码" v-model="old">
        <div class="alert alertuname"></div>
      </div>
      <div class="upwd">
        <span>新密码:</span>
        <input id="upwd" type="password" autocomplete="off" placeholder="请输入新密码" v-model="newp">
        <div class="alert alertupwd"></div>
      </div>
      <div class="again">
        <span>确认密码:</span>
        <input id="againupwd" type="password" autocomplete="off" placeholder="请输入新密码" v-model="again">
        <div class="alert alertagain"></div>
      </div>
      <div class="btn" id="login" @click="submit">确认更改</div>
    </form>
    <div class="success">修改密码成功，即将跳转登录页</div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data(){
    return{
      old:"",
      newp:"",
      again:"",
    }
  },
  methods:{
    submit(){
      if(this.old==""){
        this.$message.error("旧密码不能为空");
        return
      }

      if(this.newp==""){
        this.$message.error("新密码不能为空");
        return
      }
      if(this.again==""){
        this.$message.error("确认密码不能为空");
        return
      }
      if(this.again!=this.newp){
        this.$message.error("两次密码不一致");
        return
      }
      if(this.old==this.newp){
        this.$message.error("新旧密码不能一致");
        return
      }
      let url="http://zhuishu.zhiask.cn/api";
      let data={
         "action": "mod_pwd",
          "data": {
              "id":sessionStorage.getItem("id"),
              "password": this.old,
              "password_new":this.again
          }
      }
      let token=sessionStorage.getItem("token")
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      console.log(data);
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,
         "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        if(res.data.errcode==0){
          this.$message({
            message: '修改成功',
            type: 'success'
          });
          this.$router.push({path:"/"})
        }else{
          this.$message.error(res.data.errmsg);
        }
      })
    }
  }
}
</script>
<style>
  a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li{
    margin:0px;
    padding:0px;
  }
  .login{
    width: 100%;
    height: 100%;
    background-color: rgba(136, 134, 134, 1);
    text-align: center;
    position: absolute;
  }
  .login form{
    width: 470px;
    height: 200px;
    text-align: center;
    position: absolute;
    top:50%;
    left:50%;
    margin-top:-210px;
    margin-left: -250px;
    font-size: 16px;
  }
  .login .title{
    color: rgba(255, 255, 255, 1);
    font-size: 28px;
    font-family: SourceHanSansSC-regular;
    margin-bottom: 40px;
  }
  .login .name,.login .upwd,.login .again{
    background:#fff;
    height: 60px;
    text-align: left;
    padding: 0px 0px 10px 30px;
  }
  .login .name{
    padding-top:40px;
  }
  .login .again{
    padding-bottom:70px;
  }
  .login span{
    display: inline-block;
    width: 84px;
    height: 29px;
    color: rgba(16, 16, 16, 1);
    font-family: SourceHanSansSC-regular;
  }
  .login form input{
    width: 300px;
    height: 40px;
    color: rgba(136, 136, 136, 1);
    font-family: Roboto;
    margin-left: 20px;
    outline: none;
    padding-left:8px;
  }
  .login .btn{
    width: 114px;
    height: 35px;
    border-radius: 4px;
    color: rgba(16, 16, 16, 1);
    text-align: center;
    font-family: Microsoft Yahei;
    border: 1px solid rgba(187, 187, 187, 1);
    line-height: 35px;
    position: absolute;
    top:330px;
    left: 139px;
    cursor: pointer;
  }
  .login .alert{
    margin-left:120px;
    color:red;
  }
  .success{
    width:300px;
    height: 40px;
    position: absolute;
    top:50%;
    left:50%;
    margin-left:-150px;
    margin-top:-20px;
    background: #e5e5e5;
    line-height: 40px;
    border-radius: 8px;
    display:none;
  }
</style>



