<template>
  <div id="index">
    <header class="header">
      <span class="logo">小说后台管理系统</span>
      <div class="h-right">
        <div class="changeUpwd" @click="$router.replace({path:'/changupwd'})">修改密码</div>
        <div class="logout"  @click="loginout">退出登录</div>
        <div class="userName">admin</div>
        <div class="headImg">
          <img src="./assets/user.png" alt="">
        </div>
      </div>
    </header>
    <!-- 左边框 -->
    <div id="content">
      <div class="left" >
        <ul class="left-list">
          <router-link class="left-list-item book" to="BookManagement">书籍管理</router-link>
          <router-link class="left-list-item plan" to="acquisitionScheme">采集方案</router-link>
          <router-link class="left-list-item error" to="errorLog">错误日志</router-link>
        </ul>
      </div>
      <div id="right">
        <keep-alive>
          <router-view v-if="$route.meta.keepAlive"></router-view>
        </keep-alive>
        <router-view v-if="!$route.meta.keepAlive"></router-view>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      path:"",
      height:""
    }
  },
  methods: {
    loginout(){
      sessionStorage.clear();
      this.$router.push("/")
    }
  },
  mounted(){
    let r=document.getElementById("content");
    console.log(r)
    this.height=r.offsetHeight;
    console.log(r.offsetHeight)
  }
}
</script>
<style>
  a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li{
  margin:0px;
  padding:0px;
}
#id{
  height: 100%;
}
header{
  width:100%;
  height: 60px;
  line-height: 60px;
  background-color: rgba(136, 134, 134, 1);
  position: relative;
  color:#fff;
}
header .logo{
  display: inline-block;
  width: 200px;
  height: 29px;
  color: rgba(255, 255, 255, 1);
  font-size: 20px;
  text-align: left;
  margin-left:22px;
  font-family: SourceHanSansSC-regular;
}
header .h-right{
  display:flex;
  width: 100%;
  flex-direction: row-reverse;
  position: absolute;
  top:0px;
  right:0px;
  font-size: 14px;
}
header .h-right div{
  margin-right:20px;
  cursor: pointer;
}
#content{
  display: flex;
}
.left{
  width: 150px;
  height:100%;
  line-height: 20px;
  position: absolute;
  background-color: rgba(81, 78, 78, 1);
}
.left-list-item{
  display:block;
  width: 105px;
  height: 45px;
  line-height: 45px;
  color:#fff;
  font-size: 14px;
  font-family: Roboto;
  padding-left:35px;
  cursor: pointer;
  text-decoration: none;
}
#right{
  width: 89vw;
  height: 100%;
  position: absolute;
  overflow-x: auto;
  left: 150px;
}
.header .headImg {
  margin-right:5px;
  position: relative;
  top:1px;
}
.header .headImg img{
  width: 15px;
  height:15px;
}
</style>


