<template>
  <div class="book-management">
    <div class="bm-top">
      <div class="top">
        <input type="text" placeholder="请输入书籍的名称" v-model="bookName" @keyup.enter="search">
        <div class="btn search-box" @click="search">搜索书籍</div>
        <div class="btn recent-updates" @click="$router.push({path:'recentUpdates'})">最近更新</div>
      </div>
      <div class="search-result">
        <div class="title">搜索结果</div>
        <div class="hr"></div>
        <table v-loading="loading">
          <thead >
            <td class="table-head book-name">书名</td>
            <td class="table-head author">作者</td>
            <td class="table-head source">来源</td>
            <td class="table-head update-time">更新时间</td>
            <td class="table-head latest-chapters">最新章节</td>
          </thead>
          <tr  v-for="(item,index) in searchbook" :key="index"  @click="tiao(item.id)">
            <td class="book-name">
              <span>{{item.title}}</span>
            </td>
            <td class="author">{{item.author}}</td>
            <td class="source">{{item.source=="shenqi"?"追书神器":"追书网"}}</td>
            <td class="update-time">{{item.update_time}}</td>
            <td class="latest-chapters">{{item.recent_chapter}}</td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data(){
    return{
      bookName:"",
      searchbook:"",
      result:true,
      loading:false
    }
  },
  methods:{
    tiao(id){
      $router.push({path:`bookDetails?id=${id}`})
    },
    search(){
      if(this.bookName==""){
        this.$alert('书名不能为空','提示', {
          confirmButtonText: '确定',
        });
        return;
      }
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "search_book",
        "data": {
         "keywords":this.bookName,
        }
      }
      let token=sessionStorage.getItem("token");

      if(token==""||token==undefined){
        this.$router.push("/")
      }
      console.log(data);
      this.loading=true;
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
         "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        this.loading=false;
        console.log(res.data.data);
        this.searchbook=res.data.data;
        if(res.data.data.length==0){
          this.$message.error("没有搜索到该书籍");
        }
      })
    }
  },
  created(){
  }
}
</script>
<style>
  body{
  box-sizing: border-box;
}
a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li{
  margin:0px;
  padding:0px;
}

.bm-top{
  padding:21px 27px 0px 27px;
  font-family: Microsoft Yahei;
  color: rgba(136, 136, 136, 1);
}
.bm-top .top{
  display:flex;
  font-size: 14px;
  margin-bottom: 18px;
}
.bm-top input{
  width: 676px;
  height: 42px;
  font-size: 14px;
  text-align: left;
  border: 1px solid rgba(187, 187, 187, 1);
  padding-left:10px;
  border-radius: 8px;
  outline: none;
  
}
.bm-top .btn{
  display: inline-block;
  width: 134px;
  height: 41px;
  border-radius: 8px;
  font-size: 16px;
  text-align: center;
  border: 1px solid rgba(187, 187, 187, 1);
  line-height: 41px;
  margin-left:17px;
  cursor: pointer;
}
.bm-top .recent-updates{
  margin-left:22%;
}
.search-result{
  color: rgba(16, 16, 16, 1);
}
.search-result .title{
  font-size: 14px;
}
.search-result .hr{
  width: 100%;
  margin-top:18px;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
}
.search-result table{
  width: 100%;
  text-align: center;
  border: 1px solid rgba(187, 187, 187, 1);
  margin-top:15px;
  border-collapse: separate;
  border-spacing: 0px 0px;
  font-size: 13px;
  cursor: pointer;
}
.search-result table thead, .search-result table td{
  height: 46px;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
}
.search-result table .table-head{
  height:42px;
  background-color: rgba(247, 247, 247, 1);
}
.search-result .author{
  width: 18%;
}
.search-result .source{
  width: 18%;
}
.search-result .update-time{
  width: 18%;
}
.search-result .latest-chapters{
  width: 26%;
}
.search-result table .book-name .book-img{
  width: 33px;
  height: 33px;
  display: inline-block;
  vertical-align: middle;
}
.search-result table .book-name .book-img img{
  width: 100%;
  height: 100%;
}
.search-result .msg{
  text-align: center;
  font-size: 20px;
  padding:10px 0;
}
</style>


