import Vue from 'vue'
import Index from '@/index'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Login from "@/components/Login";
import BookManagement from "@/components/bookManagement";
import acquisitionScheme from "@/components/acquisitionScheme";
import errorLog from "@/components/errorLog";
import recentUpdates from "@/components/recentUpdates";
import bookDetails from "@/components/bookDetails";
import taskManagement from "@/components/taskManagement";
import atm from "@/components/atm";
import testCollection from "@/components/testCollection";
import collectionList from "@/components/collectionList";
import taskDetails from "@/components/taskDetails";
import changupwd from "@/components/changupwd";
import chapterDetails from "@/components/chapterDetails";
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '',
      name:'Login',
      component: Login,
      meta: {
        title: '登录'
      }
    },
    {
      path: '/taskDetails',
      name: '/taskDetails',
      component: taskDetails,
      meta: {
        title: '任务详情'
      }
    },
    {
      path: '/changupwd',
      name: '/changupwd',
      component: changupwd,
      meta: {
        title: '更改密码'
      }
    },
    {
      path: '/Index',
      name: 'Index',
      component: Index,
      children:[
        {
          path: 'BookManagement',
          name: 'BookManagement',
          component: BookManagement,
        },
        {
          path: 'acquisitionScheme',
          name: 'acquisitionScheme',
          component: acquisitionScheme,
        },
        {
          path: 'errorLog',
          name: 'errorLog',
          component: errorLog,
        },
        {
          path: 'recentUpdates',
          name: 'recentUpdates',
          component: recentUpdates,
        },
        {
          path: 'bookDetails',
          name: 'bookDetails',
          component: bookDetails,
        },
        {
          path: 'taskManagement',
          name: 'taskManagement',
          component: taskManagement,
        },
        {
          path: 'atm',
          name: 'atm',
          component: atm,
        },
        {
          path: 'testCollection',
          name: 'testCollection',
          component: testCollection,
        },
        {
          path: 'collectionList',
          name: 'collectionList',
          component: collectionList,
        },
        {
          path: 'chapterDetails',
          name: 'chapterDetails',
          component: chapterDetails,
        },
      ]
    },
    
  ]
})



