<template>
 <div class="acquisition-scheme">
    <div class="title">采集方案</div>
    <div class="hr"></div>
    <div class="programme">
      <div class="item" v-loading="loading" v-for="(item,index) in list" :key="index" @click="detail(index)">{{item.name}}</div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data(){
    return{
      list:[],
      loading:false,
    }
  },
  methods:{
    getlist(){
      this.loading=true;
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "list_crawler",
        "data": {
          time:132
        }
      }
      let token=sessionStorage.getItem("token")
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      console.log(data);
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
         "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        this.loading=false;
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        if(res.data.errcode==0){
          console.log(res.data.data);
          this.list=res.data.data;
        }else{
          this.$message.error(res.data.errmsg)
        }
      })
    },
    detail(index){
      let i=index;
      console.log(i);
      sessionStorage.setItem("taskmsg",JSON.stringify(this.list[i]));
      this.$router.push({path:'taskManagement'})
    }
  },
  created(){
    this.getlist();
  }
}
</script>
<style>
  a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li{
  margin:0px;
  padding:0px;
}
.acquisition-scheme{
  width: 100%;
  padding:30px 32px 0px 32px;
  font-family: SourceHanSansSC-regular;
}
.acquisition-scheme .title{
  width: 80px;
  height: 29px;
  color: rgba(16, 16, 16, 1);
  font-size: 20px;
}
.acquisition-scheme .hr{
  border-bottom: 1px solid rgba(187, 187, 187, 1);
  width: 1173px;
  margin:20px 0;
}
.acquisition-scheme .programme{
  width: 1173px;
  display: flex;
  cursor: pointer;
}
.acquisition-scheme .programme .item{
  width: 150px;
  height: 100px;
  line-height: 100px;
  color: rgba(16, 16, 16, 1);
  font-size: 18px;
  text-align: center;
  font-family: Roboto;
  border: 1px solid rgba(187, 187, 187, 1);
  margin-right: 30px;
}
</style>


