<template>
  <div>
    <header class="header">
      <span class="logo">小说后台管理系统</span>
      <div class="h-right">
        <div class="changeUpwd">修改密码</div>
        <div class="logout">退出登录</div>
        <div class="userName">admin</div>
        <div class="headImg">
            <img src="../assets/user.png" alt="">
        </div>
      </div>
    </header>
    <div class="left" >
      <ul class="left-list">
        <li class="left-list-item book">书籍管理</li>
        <li class="left-list-item plan">采集方案</li>
        <li class="left-list-item error">错误日志</li>
      </ul>
    </div>
    <div id="right" v-loading="loading">
      <div class="task-detail">
          <div class="title">{{source=="shenqi"?"追书神器":"追书网"}}源采集中...</div>
          <div class="num">共<span>{{taskdetails.book_cnt}}</span>本 当前采集第<span>{{taskdetails.book_idx}}</span>本</div>
          <div class="msg">
            <div class="start-time">开始时间：<span>{{taskdetails.start_time}}</span> </div>
            <div class="running-time">运行时间：<span>{{time}}</span></div>
            <div class="updatad">共更新书：<span>{{taskdetails.book_total}}</span></div>
            <div class="updata-chapter">共更章节：<span>{{taskdetails.chapter_total}}</span></div>
          </div>
          <div class="msg now" style="">
            <div>当前小说：<span>{{taskdetails.book_title}}</span> </div>
            <div style="width:800px;height:100%;word-break:break-all;word-wrap:break-word;overflow:hidden;">源链接：{{taskdetails.book_link}}</div>
            <div>当前章节：<span>{{taskdetails.chapter_title}}</span></div>
            <div>共需采集：<span>{{taskdetails.chapter_cnt}}</span></div>
            <div>已采集&#12288;：<span>{{taskdetails.chapter_idx}}</span></div>
            <div>当前状态：<span>{{status}}</span></div>
          </div>
        </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data(){
    return{
      bid:"",
      taskdetails:"",
      status:"",
      time:"",
      source:"",
      t:"",
      fanganid:"",
      renwuid:"",
      loading:false,
      t1:"",
      title:"",
    }
  },
  methods:{
    getdata(){
      let token=sessionStorage.getItem("token");
      if(token==""||token==undefined){
        this.$router.push("/")
      }
      // let cid=JSON.parse( sessionStorage.getItem("taskmsg")).id;
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "task_detail",
        "data": {
          "sid":this.fanganid,//采集方案id
          "tid": this.renwuid||0,//采集任务id
          "bid": this.bid||0,//书籍aid
          "title":this.title
        }
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          clearInterval(this.t);
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        if(res.data.errcode==5){
          clearInterval(this.t);
          this.$message({
            message: '采集完成',
            type: 'success'
          });
          console.log(this.status,123)
          if(this.$route.query.type==0){
            this.status="采集完毕 页面停止刷新"
          }else{
          }
          this.status="采集完毕，等待新的采集..."
        }else{
          this.loading=false;
          this.taskdetails=res.data.data;
          if(this.taskdetails.status==0){
            this.status="正常"
          }else if(this.taskdetails.status==1){
            this.status="正在采集"
          }else if(this.taskdetails.status==2){
            this.status="未找到源"
          }else if(this.taskdetails.status==3){
            this.status="基本信息采集完毕"
          }else if(this.taskdetails.status==4){
            this.status="已下架"
          }else if(this.taskdetails.status==5){
            this.status="章节目录采集完毕"
          }else if(this.taskdetails.status==6){
            this.status="章节目录采集失败"
          }else if(this.taskdetails.status==7){
            this.status="内容采集完毕"
          }else if(this.taskdetails.status==9){
            this.status="采集完毕，等待新的采集..."
          }else{
            this.status="内容采集失败"
          }
          let miao=this.taskdetails.run_time;
          let hour= Math.floor(miao/3600);
          let min=Math.floor((miao-(hour*3600))/60);
          let sec=miao-hour*3600-min*60;
          this.time=`${hour}小时${min}分钟${sec}秒`
          clearInterval(this.t);
          console.log(this.status);
          this.set();
        }
      })
    },
    first(){
      this.loading=true;
      let token=sessionStorage.getItem("token");
      if(token==""||token==undefined){
        this.$router.push("/")
      }
      // let cid=JSON.parse( sessionStorage.getItem("taskmsg")).id;
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "task_detail",
        "data": {
          "sid":this.fanganid,//采集方案id
          "tid": this.renwuid||0,//采集任务id
          "bid": this.bid||0,//书籍aid
          "title":this.title
        }
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          clearInterval(this.t);
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        if(res.data.errcode==5){
          clearInterval(this.t1);
          clearInterval(this.t);
          this.firstset();
        }else{
          this.loading=false;
          this.taskdetails=res.data.data;
          if(this.taskdetails.status==0){
            this.status="正常"
          }else if(this.taskdetails.status==1){
            this.status="正在采集"
          }else if(this.taskdetails.status==2){
            this.status="未找到源"
          }else if(this.taskdetails.status==3){
            this.status="基本信息采集完毕"
          }else if(this.taskdetails.status==4){
            this.status="已下架"
          }else if(this.taskdetails.status==5){
            this.status="章节目录采集完毕"
          }else if(this.taskdetails.status==6){
            this.status="章节目录采集失败"
          }else if(this.taskdetails.status==7){
            this.status="内容采集完毕"
          }else{
            this.status="内容采集失败"
          }
          let miao=this.taskdetails.run_time;
          let hour= Math.floor(miao/3600);
          let min=Math.floor((miao-(hour*3600))/60);
          let sec=miao-hour*3600-min*60;
          this.time=`${hour}小时${min}分钟${sec}秒`
          clearInterval(this.t1)
          this.set();
        }
      })
    },
    // 每一秒
    set(){
      let that=this;
      this.t=setInterval(function(){
        that.getdata();
        console.log(456)
      },2000)
    },
    firstset(){
      let that=this;
      this.t1=setInterval(function(){
        that.first();
        console.log(123)
      },2000)
    },
    beforeDestroy() {
      clearInterval(this.t);
      clearInterval(this.t1);
    },
  },
  created(){
    if(this.$route.query.type==0){
      this.fanganid = parseInt(this.$route.query.fanganid);
      this.source =  this.$route.query.source;
      this.bid =parseInt(this.$route.query.bid);
      this.title=this.$route.query.title;
      this.first();
    }else{
      this.source=JSON.parse(sessionStorage.getItem("taskmsg")).source;
      this.fanganid=parseInt(JSON.parse(sessionStorage.getItem("taskmsg")).id);
      this.renwuid=parseInt(this.$route.query.bid);
      this.title=this.$route.query.title;
      this.getdata();
    }
    
    
  },
  mounted() {
    document.title=this.title
  },
}
</script>
<style>
  a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li{
  margin:0px;
  padding:0px;
}
header{
  width:100%;
  height: 60px;
  line-height: 60px;
  background-color: rgba(136, 134, 134, 1);
  position: relative;
  color:#fff;
}
header .logo{
  display: inline-block;
  width: 200px;
  height: 29px;
  color: rgba(255, 255, 255, 1);
  font-size: 20px;
  text-align: left;
  margin-left:22px;
  font-family: SourceHanSansSC-regular;
}
header .h-right{
  display:flex;
  width: 100%;
  flex-direction: row-reverse;
  position: absolute;
  top:0px;
  right:0px;
  font-size: 14px;
}
header .h-right div{
  margin-right:20px;
  cursor: pointer;
}
.left{
  width: 150px;
  height:100%;
  line-height: 20px;
  position: absolute;
  background-color: rgba(81, 78, 78, 1);
  top:60px;
}
.left-list-item{
  width: 105px;
  height: 45px;
  line-height: 45px;
  color:#fff;
  font-size: 14px;
  font-family: Roboto;
  padding-left:35px;
  cursor: pointer;
}
#right{
  width: 89vw;
  height: 100%;
  position: absolute;
  left:150px;
}
.header .headImg {
  margin-right:5px;
  position: relative;
  top:1px;
}
.header .headImg img{
  width: 15px;
  height:15px;
}
.task-detail{
  width: 97%;
  margin-left: 32px;
  padding-top:30px;
}
.task-detail .title{
  height: 54px;
  color: rgba(16, 16, 16, 1);
  font-size: 36px;
  text-align: left;
  font-family: SourceHanSansSC-regular;
}
.task-detail .num{
  height: 29px;
  color: rgba(16, 16, 16, 1);
  font-size: 20px;
  padding-bottom: 10px;
  margin-top:5px;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
}
.task-detail .msg{
  width: 234px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  text-align: left;
  font-family: SourceHanSansSC-regular;
  margin:15px 0px;
}
.task-detail .msg div{
  line-height: 30px;
}
.task-detail .now{
  width: 600px;
  margin:0px;
}
</style>


