<template>
  <div class="atm" v-loading="loading">
    <div class="top">
      <div class="back" @click="back">返回</div>
      <div class="creat" @click="xinjian">新建采集任务</div>
      <div class="clear" @click="remove">清空采集任务</div>
    </div>
    <table >
      <thead>
        <td class="head name" >任务名称</td>
        <td class="head source" >采集源</td>
        <td class="head cz">操作</td>
      </thead>
      <tr v-for="(item,index) in fenyelist" :key="index" >
        <td class="name" >{{item.title}}</td>
        <td class="source" >{{item.source=="shenqi"?"追书神器":"追书网"}}</td>
        <td class="cz">
          <div class="see" @click="see(item.id,item.title)">查看</div>
          <div class="del" @click="deleta(index)">删除</div>
        </td>
      </tr>
    </table>
    <div class="fenye">
       <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNum+1"
        :page-sizes="[10, 20, 30, 40, 50, 100]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data(){
    return{
      taskmsg:"",//采集方案信息
      pageNum:0,//页码
      pageSize:10,
      total:0,//总数
      list:[],
      t:"",
      loading:false,
      fenyelist:"",
    }
  },
  methods:{
    back(){
      this.$router.back()
    },
    gettasklist(){
      let token=sessionStorage.getItem("token");
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "list_task",
        "data": {
          "id": this.taskmsg.id,
          // "page": this.pageNum,
          // "num": this.pageSize
        }
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        // if(this.list.length==0){
        //   this.loading=false;
        //   clearInterval(this.t)
        // }else{
        //   console.log(132)
        // }
        if(res.data.errcode==3){
          this.$router.push("/")
        }
        console.log(this.total==res.data.total,res.data.total)
        if(this.total!=res.data.total){
          this.loading=false;
          clearInterval(this.t)
        }
        this.list=res.data.data;
        this.fenyelist=res.data.data.slice(this.pageNum*this.pageSize,(this.pageNum+1)*this.pageSize);
        this.total=res.data.total;
        
      })
    },
    //分页
    handleSizeChange(e){
      this.pageSize=e;
      this.fenyelist=this.list.slice(this.pageNum*this.pageSize,(this.pageNum+1)*this.pageSize);
    },
    handleCurrentChange(e){
      this.pageNum=e-1;
      this.fenyelist=this.list.slice(this.pageNum*this.pageSize,(this.pageNum+1)*this.pageSize);
    },
    //查看
    see(bid,title){
      let routeData = this.$router.resolve({
        name: "/taskDetails",
        query: {bid:bid,type:1,title:title},
      });
      window.open(routeData.href, '_blank');
    },
    //删除
    deleta(index){
      this.$confirm('此操作将永久删除该任务, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.loading=true;
        let token=sessionStorage.getItem("token");
              if(token==""||token==undefined){
          this.$router.push("/")
        }
        let url="http://zhuishu.zhiask.cn/api";
        let data={
          "action": "del_task",
          "data": {
              "id": this.taskmsg.id,
              "task": this.list[index].id
          }
        }
        axios({
          method: 'post',
          url: url,
          data: JSON.stringify(data),
          headers:{
            "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
            "Access-Token":token,             'cache-control': 'no-cache',
          },
          contentType: "text/html;charset=utf-8",
          dataType: "json",
        })
        .then(res=>{
                  if(res.data.errcode==3){
            this.$router.push("/")
          }
          console.log(res.data.data);
          if(res.data.errcode==0){
            this.$message({
              message: '正在删除中',
              type: 'success'
            });
            this.set();
            if(this.list.length!=res.data.data.length){
              console.log(132)
              this.loading=false;
              clearInterval(this.t)
            }
          }else{
            this.$message.error(res.data.errmsg);
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });          
      });
    },
    set(){
      let that=this;
      this.t=setInterval(function(){
        that.gettasklist();
      },2000)
    },
    //新建采集
    xinjian(){
      this.loading=true;
      let token=sessionStorage.getItem("token");
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "add_task",
        "data": {
            "id": this.taskmsg.id,
        }
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          this.$router.push("/")
        }
        console.log(res.data.data);
        if(res.data.errcode==0){
          this.$message({
            message: '正在新建，请稍后',
            type: 'success'
          });
          clearInterval(this.t)
          this.set();
        }else{
          this.$message.error(res.data.errmsg);
        }
      })
    },
    //清空采集任务
    remove(){
      this.loading=true;
      let token=sessionStorage.getItem("token");
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "cls_task",
        "data": {
            "id": this.taskmsg.id,
        }
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        console.log(res.data.data);
        if(res.data.errcode==0){
          this.$message({
            message: '正在清空',
            type: 'success'
          });
          this.set();
          if(this.list.length!=res.data.data.length){
            console.log(132)
            this.loading=false;
            clearInterval(this.t)
          }
          this.list.splice(index,1)
        }else{
          this.$message.error(res.data.errmsg);
        }
      })
    }
  },
  created(){
    this.taskmsg=JSON.parse(sessionStorage.getItem("taskmsg")) 
    this.gettasklist();
  },
  beforeDestroy() {
    clearInterval(this.t)
  },
}
</script>
<style>
  a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li{
  margin:0px;
  padding:0px;
}
.atm{
  width: 97%;
  margin-left: 32px;
  padding-top:30px;
}
.atm .top{
  display:flex;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
}
.atm .top div{
  width: 84px;
  height: 25px;
  line-height: 25px;
  border-radius: 6px;
  color: rgba(16, 16, 16, 1);
  font-size: 12px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  margin-right: 35px;
  cursor: pointer;
}
.atm table{
  width: 100%;
  border-collapse: collapse;
  border: 1px solid rgba(187, 187, 187, 1);
}
.atm table td{
  border-collapse: collapse;
  text-align: center;
  font-size: 14px;
  height: 34px;
  padding:5px 0px;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
}
.atm table .head{
  height: 34px;
  line-height: 34px;
  background-color: rgba(247, 247, 247, 1);
  text-align: center;
}
.atm table .name{
  width: 30%;
  color: rgba(16, 16, 16, 1);
  font-family: SourceHanSansSC-regular;
}
.atm table .source{
  width: 30%;
}
.atm table .cz{
  width: 40%;
}
.atm table .cz div{
  display: inline-block;
  width: 60px;
  height: 20px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  font-size: 13px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  cursor: pointer;
  line-height: 20px;
}
.atm table .cz .see{
  margin-right: 10px;
}
.fenye{
  width:600px;
  display: flex;
  margin:50px auto;
}
</style>


