<template>
  <div class="error-log">
    <div class="top">
      <div class="title">错误日志</div>
      <div class="right">
        <div class="all" @click="chuli()">全部标记为已处理</div>
        <span>错误类型</span>
        <el-select v-model="type" placeholder="请选择" size="mini">
          <el-option
            v-for="item in errortype"
            :key="item.n"
            :label="item.name"
            :value="item.n">
          </el-option>
        </el-select>
        <span>错误状态</span>
        <el-select v-model="status" placeholder="请选择" size="mini">
          <el-option
            v-for="item in statuslist"
            :key="item.i"
            :label="item.title"
            :value="item.i">
          </el-option>
        </el-select>
        <div class="search" @click="search">搜索</div>
      </div>
    </div>
    <table v-loading="loading">
      <thead>
        <td class="head num">序号</td>
        <td class="head name">书名</td>
        <td class="head zhang">章节</td>
        <td class="head time">日志产生时间</td>
        <td class="head describe">错误描述</td>
        <td class="head type">错误状态</td>
        <td class="head operation">操作</td>
      </thead>
      <tr v-for="(item,index) in list" :key="index" @click="$router.push({path:`bookDetails?id=${item.bid}`})">
        <td class="num">{{item.id}}</td>
        <td class="name">{{item.title}}</td>
        <td class="zhang" >{{item.chapter}}</td>
        <td class="time" >{{item.event_time}}</td>
        <td class="describe" >{{item.desc}}</td>
        <td class="type" >{{item.status==1?"已处理":"未处理"}}</td>
        <td class="operation">
          <div class="see" @click="$router.push({path:`bookDetails?id=${item.bid}`})">查看</div>
          <div class="yichuli" v-if="item.status==0" @click="chuli(item.id,index)">已处理</div>
        </td>
      </tr>
    </table>
    <div class="fenye">
       <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNum+1"
        :page-sizes="[10, 20, 30, 40, 50, 100]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data(){
    return{
      pageNum:1,//页码
      pageSize:10,
      total:0,//总数
      list:[],
      loading:false,
      errortype:[
        {
          "n":"",
          "name":"全部"
        },
        {
          "n":2,
          "name":"未知错误"
        },
        {
          "n":3,
          "name":"FTP负载错误"
        },
        {
          "n":4,
          "name":"对比最新章节失败"
        },
        {
          "n":5,
          "name":"空章节"
        },

        {
          "n":6,
          "name":"检测到重复章节名"
        },
        {
          "n":7,
          "name":"章节顺序混乱"
        },
        {
          "n":8,
          "name":"章节字数少于500字"
        },
        {
          "n":10,
          "name":"小说信息发生错误"
        },
        {
          "n":11,
          "name":"小说目录发生问题"
        },
        {
          "n":12,
          "name":"数据库操作发生问题"
        },
        {
          "n":13,
          "name":"书名，作者，简介等不一致"
        },
      ],
      type:"",//错误类型
      statuslist:[
        {
          "i":"",
          "title":"全部"
        },
        {
          "i":0,
          "title":"未处理"
        },
        {
          "i":1,
          "title":"已处理"
        },
      ],
      status:"",
      pageNum:0,//页码
      pageSize:10,
      total:0,//总数
    }
  },
  methods:{
    getlist(){
      this.loading=true;
      let token=sessionStorage.getItem("token");
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "list_logging",
        "data": {
          "page": this.pageNum,
          "num": this.pageSize
        }
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        this.loading=false;
        console.log(res.data.data);
        this.list=res.data.data;
        this.total=res.data.total;
        if(res.data.data.length==0){
          this.$message.error("没有错误信息")
        }
      })
    },
    search(){
      this.loading=true;
      let token=sessionStorage.getItem("token");
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "search_logging",
        "data": {
          "page": this.pageNum,
          "num": this.pageSize,
          "type": this.type,
          "status":this.status,
        }
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        this.loading=false;
        console.log(res.data.data);
        this.list=res.data.data;
        this.total=res.data.total;
        if(res.data.data.length==0){
          this.$message.error("没有符合该条件的错误信息")
        }
      })
    },
    chuli(id,i){
      console.log(id,i);
      let token=sessionStorage.getItem("token");
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "mod_logging",
        "data": {
          "id":id
        }
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        if(res.data.errcode==0){
          this.$message({
            message: '处理成功',
            type: 'success'
          });
          if(i){
            this.list[i].status=1;
          }
          if(id==undefined){
            for(let a=0;a<this.list.length;a++){
              this.list[a].status=1
            }
          }
        }else{
          this.$message.error(res.data.errmsg);
        }
      })
    },
    handleSizeChange(e){
      this.pageSize=e;
      if(this.type!=""||this.status!=""){
        this.search();
      }else{
        this.getlist();
      }
      
    },
    handleCurrentChange(e){
      this.pageNum=e-1;
      if(this.type!=""||this.status!=""){
        this.search();
      }else{
        this.getlist();
      }
    },
    see(bid){
      let routeData = this.$router.resolve({
        name: "/taskDetails",
        query: {bid:bid},
      });
      window.open(routeData.href, '_blank');
    }
  },
  created(){
      this.getlist();
  }
}
</script>
<style>
  a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li{
  margin:0px;
  padding:0px;
}
.error-log{
  width: 97%;
  margin-left: 32px;
  padding-top:30px;
}
.error-log .top{
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
  padding-bottom: 10px;
}
.error-log .top .title{
  color: rgba(16, 16, 16, 1);
  font-size: 18px;
  text-align: left;
  font-family: SourceHanSansSC-regular;
}
.error-log .top .right{
  display: flex;
  align-items: baseline;
  font-size: 13px;
}
.error-log .top .right .all{
  width: 136px;
  height: 23px;
  line-height: 23px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  margin-right: 12px;
  cursor: pointer;
}
.error-log .top .right span{
  color: rgba(16, 16, 16, 1);
  text-align: left;
  font-size: 15px;
  font-family: SourceHanSansSC-regular;
  margin:0 10px;
}
.error-log .top .right #type{
  width: 200px;
}
.error-log .top .right .search{
  width: 80px;
  height: 23px;
  line-height: 23px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  cursor: pointer;
  margin-left:10px;
}
.error-log table{
  width: 100%;
  border-collapse: collapse;
  border: 1px solid rgba(187, 187, 187, 1);
  font-size: 13px;
  margin-top:20px;
}
.error-log table td{
  padding:8px 0;
  line-height: 20px;
  text-align: center;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
  cursor: pointer;
}
.error-log table .head{
  background-color: rgba(247, 247, 247, 1);
}
.error-log table .num{
  width: 5%;
}
.error-log table .name{
  width: 8%;
}
.error-log table .zhang{
  width: 30%;
}
.error-log table .time,.error-log table .describe,.error-log table .type{
  width: 13%;
}
.error-log table .operation div{
  display: inline-block;
  cursor: pointer;
  width: 70px;
  height: 25px;
  line-height: 25px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
}
.error-log table .operation .yichuli{
  margin-left:10px;
}
.fenye{
  width:600px;
  display: flex;
  margin:50px auto;
}
</style>


