<template>
  <div class="collection-list">
    <div class="head">
      <p>采集清单</p>
      <span>(共{{total}}本)</span>
      <div class="back" @click="back">返回</div>
      <div class="btn" @click="$router.push({path:'testCollection'})">测试采集</div>
    </div>
    <div class="search">
      <input type="text" placeholder="搜索采集清单" v-model="clist">
      <div class="search-btn" @click="search">搜索</div>
    </div>
    <table>
      <thead >
        <td class="table-head book-name">书名</td>
        <td class="table-head author">作者</td>
        <td class="table-head source">来源</td>
        <td class="table-head update-time">更新时间</td>
        <td class="table-head latest-chapters">最新章节</td>
      </thead>
      <tr v-for="(item,index) in list" :key="index" @click="tiao(item.id)">
        <td class="book-name">
          <span>{{item.title}}</span>
        </td>
        <td class="author">{{item.author}}</td>
        <td class="source">{{item.source=="shenqi"?"追书神器":"追书网"}}</td>
        <td class="update-time">{{item.update_time}}</td>
        <td class="latest-chapters">{{item.recent_chapter}}</td>
      </tr>
    </table>
     <div class="fenye">
       <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageNum+1"
        :page-sizes="[10, 20, 30, 40, 50, 100]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data(){
    return{
      clist:"",//搜索项
      list:"",//结果
      pageNum:0,//页码
      pageSize:10,
      total:0,//总数
    }
  },
  methods:{
    back(){
      this.$router.back();
    },
    tiao(id){
      this.$router.push({path:`bookDetails?id=${id}`});
    },
    search(){
      this.pageNum=0;
      this.pageSize=10;
      let token=sessionStorage.getItem("token");
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "search_crawling",
        "data": {
            "keywords": this.clist,
            "page": this.pageNum,
            "num": this.pageSize
        }
      }
      if(this.clist==""){
        this.$message.error("搜索清单不能为空");
        return;
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        console.log(res.data.data);
        this.list=res.data.data;
        this.total=res.data.total;
        if(res.data.data.length==0){
          this.$message.error("没有搜到该书籍")
        }
      })
    },
    getlist(){
      let taskid=JSON.parse(sessionStorage.getItem("taskmsg")).id
      let token=sessionStorage.getItem("token");
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "list_crawling",
        "data": {
            "id": taskid,
            "page": this.pageNum,
            "num": this.pageSize
        }
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        console.log(res.data.data);
        this.list=res.data.data;
        this.total=res.data.total
        if(res.data.data.length==0){
          this.$message.error("没有数据")
        }
      })
    },
    //分页
    handleSizeChange(e){
      this.pageSize=e;
      this.getlist();
    },
    handleCurrentChange(e){
      this.pageNum=e-1;
      this.getlist();
    },
  },
  created(){
    this.getlist();
  }
}
</script>
<style>
  a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li{
  margin:0px;
  padding:0px;
}
.collection-list{
  padding:30px 0px 0px 0px;
  width: 97%;
  margin-left: 32px;
}
.collection-list .head{
  width: 100%;
  display: flex;
  position: relative;
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
}
.collection-list .head p{
  width: 80px;
  height: 29px;
  color: rgba(16, 16, 16, 1);
  font-size: 16px;
  font-family: SourceHanSansSC-regular;
}
.collection-list .head .back{
  width: 80px;
  height: 23px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  line-height: 23px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  cursor: pointer;
  margin-left:10px;
}
.collection-list .head .btn{
  position: absolute;
  top:0px;
  right: 0px;
  width: 80px;
  height: 25px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  line-height: 25px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  cursor: pointer;
}
.collection-list .search{
  text-align: center;
  margin-top:20px;
  margin-left: -32px;
}
.collection-list .search input{
  width: 414px;
  height: 31px;
  color: rgba(136, 136, 136, 1);
  font-size: 14px;
  text-align: left;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  border-radius: 8px;
  outline: none;
  padding-left: 10px;
  margin-right: 10px;
}
.collection-list .search .search-btn{
  display: inline-block;
  width: 80px;
  height: 31px;
  line-height: 31px;
  border-radius: 8px;
  color: rgba(16, 16, 16, 1);
  font-size: 16px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  cursor: pointer;
}
.collection-list table{
  width: 100%;
  text-align: center;
  border: 1px solid rgba(187, 187, 187, 1);
  margin-top:15px;
  border-collapse: separate;
  border-spacing: 0px 0px;
  font-size: 13px;
}
.collection-list table td{
  height: 46px;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
  cursor: pointer;
}
.collection-list table .table-head{
  height:42px;
  background-color: rgba(247, 247, 247, 1);
}
.collection-list .author{
  width: 18%;
}
.collection-list .source{
  width: 18%;
}
.collection-list .update-time{
  width: 18%;
}
.collection-list .latest-chapters{
  width: 26%;
}
.collection-list table .book-name .book-img{
  width: 33px;
  height: 33px;
  display: inline-block;
  vertical-align: middle;
}
.collection-list table .book-name .book-img img{
  width: 100%;
  height: 100%;
}
.fenye{
  width:600px;
  display: flex;
  margin:50px auto;
}
</style>


