<template>
  <div class="chapter-details">
  <div class="top">
      <div class="back-btn" @click="back">返回</div>
    </div>
    <div class="chapte-name">
      <input type="text" placeholder="第一章  章节文字测试信息测试信息" v-model="cname">
    </div>
    <quill-editor 
        v-model="info" 
        ref="myQuillEditor" 
        :options="editorOption" 
        @blur="onEditorBlur($event)" @focus="onEditorFocus($event)"
        @change="onEditorChange($event)">
    </quill-editor>
    <div class="btn" @click="submit">提交</div>
     
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data(){
    return{
      bid:"",
      cid:"",
      cname:"",
      info:"",
      editorOption:{},
    }
  },
  methods:{
    back(){
      this.$router.back();
    },
    getdata(){
      let token=sessionStorage.getItem("token");
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      this.bid=sessionStorage.getItem("bid");
      this.cid=sessionStorage.getItem("cid");
      this.cname=sessionStorage.getItem("title");
      let url=sessionStorage.getItem("clink");
      console.log(url.slice(0,-13));
      let clink="http://zhuishu.zhiask.cn/"+url.slice(0,-13)+`${this.cid}.txt`
      axios({
        method: 'get',
        url:clink,
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token,
          'cache-control': 'no-cache', 
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        this.info=res.data;
        sessionStorage.setItem("title",this.cname)
      })
    },
    submit(){
      let token=sessionStorage.getItem("token");
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "mod_chapter",
        "data": {
          "bid": parseInt(this.bid),
          "idx": parseInt(this.cid),
          "title":this.cname,
          "content":this.info
        }
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        console.log(res.data)
        if(res.data.errcode==0){
          this.$message({
            message: '修改成功',
            type: 'success'
          });
          this.bookmsg=res.data.data;
          sessionStorage.setItem("title",this.cname);
        }else{
          this.$message.error(res.data.errmsg);
        }
      })
    },
    onEditorReady(editor) { // 准备编辑器
    },
    onEditorBlur(){}, // 失去焦点事件
    onEditorFocus(){}, // 获得焦点事件
    onEditorChange(){}, // 内容改变事件
  },
  computed: {
    editor() {
        return this.$refs.myQuillEditor.quill;
    },
  },
  created(){
    this.getdata();
  }
}
</script>
<style>
  a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li,textarea{
  margin:0px;
  padding:0px;
}
.chapter-details{
  box-sizing:border-box;
  padding:0px 32px;
}
.chapter-details .top{
  width: 100%;
  padding: 14px 0px 21px 0px;
}
.chapter-details .top .back-btn{
  width: 80px;
  height: 30px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  line-height: 30px;
  cursor: pointer;
}
.chapter-details .chapte-name{
  margin-bottom: 20px;
}
.chapter-details .chapte-name input{
  width: 485px;
  height: 33px;
  color: rgba(136, 136, 136, 1);
  font-size: 14px;
  text-align: left;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  padding-left:8px;
  border-radius: 5px;
  outline: none;
}
.chapter-details .content textarea{
  width: 726px;
  height: 299px;
  line-height: 20px;
  color: rgba(136, 136, 136, 1);
  font-size: 14px;
  text-align: left;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  resize:none;
}
.chapter-details .btn{
  width: 101px;
  height: 30px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  line-height: 30px;
  margin:22px auto;
  cursor: pointer;
}
</style>


