<template>
  <div id="login">
    <div class="login">
      <div class="form">
        <div class="title">小说采集更新管理后台系统</div>
        <div class="name">
          <span>用户名:</span>
          <input id="uname" type="text" autocomplete="off"  placeholder="请输入用户名" v-model="uname">
          <div class="alert alertuname"></div>
        </div>
        <div class="upwd">
          <span>密码:</span>
          <input id="upwd" type="password" autocomplete="off" placeholder="请输入密码" v-model="upwd" @keyup.enter="login">
          <div class="alert alertupwd"></div>
        </div>
        <div class="btn" id="login" @click="login" style="top:260px;left: 99px;">登录</div>
    </div>
  </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data(){
    return{
      uname:"",//用户名
      upwd:"",
    }
  },
  methods: {
    login(){
      let uname=this.uname;
      let upwd=this.upwd;
      if(uname==""){
        this.$alert('用户名不能为空','提示', {
          confirmButtonText: '确定',
        });
        return;
      }
      if(upwd==""){
        this.$alert('用户名不能为空','提示', {
          confirmButtonText: '确定',
        });
        return;
      }
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "login",
        "data": {
          "username": this.uname,
          "password": this.upwd
        }
      }
      console.log(data);
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "token":"132"
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        console.log(res.data);
        if(res.data.errcode!=0){
          this.$alert('用户名或密码出错','提示', {
            confirmButtonText: '确定',
          });
          return;
        }else{
          sessionStorage.setItem("token",res.data.data.token);
          sessionStorage.setItem("id",res.data.data.id);
          this.$router.push({path:'index/BookManagement'})
        }
      })

    },
  },
  mounted(){
    // history.pushState(null, null, document.URL);
    // window.addEventListener('popstate', function () {
    //     history.pushState(null, null, document.URL);
    // });
  }
}
</script>

<style>
  a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li{
  margin:0px;
  padding:0px;
}
.login{
  width: 100%;
  height: 100%;
  background-color: rgba(136, 134, 134, 1);
  text-align: center;
  position: absolute;
}
.login .form{
  width: 470px;
  text-align: center;
  position: absolute;
  top:50%;
  left:50%;
  margin-top:-150px;
  margin-left: -250px;
  font-size: 16px;
}
.login .title{
  color: rgba(255, 255, 255, 1);
  font-size: 28px;
  font-family: SourceHanSansSC-regular;
  margin-bottom: 40px;
}
.login .name,.login .upwd{
  background:#fff;
  height: 60px;
  text-align: left;
  padding: 0px 0px 10px 30px;
}
.login .form .name{
  padding-top:40px;
}
.login .form .upwd{
  padding-bottom: 70px;
}
.login span{
  display: inline-block;
  width: 54px;
  height: 29px;
  color: rgba(16, 16, 16, 1);
  font-family: SourceHanSansSC-regular;
}
.login .form input{
  width: 300px;
  height: 40px;
  color: rgba(136, 136, 136, 1);
  font-family: Roboto;
  margin-left: 10px;
  outline: none;
  padding-left:8px;
}
.login .btn{
  width: 114px;
  height: 35px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  line-height: 35px;
  position: absolute;
  top:260px;
  left: 99px;
  cursor: pointer;
}
.login .alert{
  margin-left:70px;
  color:red;
}
</style>



