<template>
  <div class="task-management">
    <div style="width:150px;font-size:14px;position:absolute;top:0px;left:40px">采集源：{{this.taskmsg.name}}</div>
    <div class="top1">
      
      <div class="left1">
        <div class="task-list back" @click="back">返回</div>
        <div class="task-list tm" @click="tiao1">采集任务管理</div>
        
      </div>
      <div class="right">
        <div class="task-list cl" @click="tiao2">采集清单</div>
        <div class="task-list test"  @click="testclo">测试采集</div>
      </div>
    </div>
    <div class="new-add">
      <div class="title">新增采集</div>
      <div class="name-zz">
        <div class="book-name">
          <span>书名</span>
          <input type="text" placeholder="单行输入" v-model="bookname">
        </div>
        <div class="book-zz">
            <span>作者</span>
            <input type="text" placeholder="单行输入" v-model="zuozhe">
          </div>
      </div>
      <div class="add-link">
        <span>添加链接采集</span>
        <textarea name="" id="" v-model="link"></textarea>
      </div>
      <div class="add-more">
        <span>添加批量采集</span>
          <input type="file" enctype="multipart/form-data"
          name="upfile" id="files" value="" />
                  
        <div>
          <div class="btn add" @click="addcaiji">新增采集</div>
          <div class="btn remove" @click="remove">清空采集源</div>
        </div>
      </div>
    </div>
    <div class="set">
      <div class="title">设置</div>
      <div class="plan-name">
        <span>采集方案管理：</span>
        <input type="text" value="" placeholder="单行输入" v-model="taskmsg.name">
      </div>
      <div class="set-zi">
        <div>
          <span>设置过滤的字</span>
          <input type="text"  class="beforeContent" placeholder="单行输入" v-model="before">
        </div>
        <div>
          <span>替换为</span>
          <input type="text" class="nowContent" placeholder="单行输入" v-model="after">
        </div>
        <div class="btn" @click="addarr(0)">添加过滤</div>
      </div>
      <ul class="set-result">
        <li v-for="(item,index) in taskmsg.filter" :key="index">
          <span>{{item.pattern}}</span>
          <span>替换为</span>
          <span>{{item.replace}}</span>
          <img src="../assets/error.png" alt="" @click="deleta(0,index)">
        </li>
      </ul>
      <div class="collection-times">
        <span>采集频率</span>
        <input type="text" placeholder="0.1" class="time" v-model="time">
        <span>秒/每次</span>
      </div>
      <div class="user">
        <div>
          <span>账号</span>
          <input type="text" class="username" placeholder="用户名" v-model="uname">
        </div>
        <div>
          <span>密码</span>
          <input type="text" class="upwd" placeholder="密码" v-model="upwd">
        </div>
        <div class="btn"  @click="addarr(1)">添加账号</div>
      </div>
      <ul class="user-result">
        <li v-for="(item,index) in taskmsg.account" :key="index">
          <span>账号：{{item.user}}</span>
          <span>，</span>
          <span>密码：{{item.pass}}</span>
          <img src="../assets/error.png" alt="" @click="deleta(1,index)">
        </li>
      </ul>
    </div>
    <div class="hr"></div>
    <div class="submit">
      <div class="btn sub" @click="submin">提交</div>
      <div class="btn back">返回</div>
    </div>
    <div class="alert"></div>
  </div>
</template>
<script>
import axios from 'axios'
import { setTimeout } from 'timers';
export default {
  data(){
    return{
      link:"",//链接
      bookname:"",//书名
      zuozhe:"",//作者
      before:"",//要过滤的
      after:"",//替换的
      time:"",//采集评率
      uname:"",//用户名
      upwd:"",//密码
      guolist:[],//过滤列表
      userlist:[],//用户列表
      taskmsg:[],
      filename:""
    }
  },
  methods:{
    tiao1(){
      this.$router.push({path:'atm'})
    },
    tiao2(){
      this.$router.push({path:'collectionList'})
    },
    back(){
      this.$router.back();
    },
    testclo(){
      let routeData = this.$router.resolve({
        name: "testCollection",
      });
      window.open(routeData.href, '_blank');
    },
    //清空采集源
    remove(){
      let token=sessionStorage.getItem("token");
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      let url="http://zhuishu.zhiask.cn/api";
      let data={
        "action": "cls_crawler",
        "data": {
            "id": this.taskmsg.id,
        }
      }
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
          "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        console.log(res.data);
        if(res.data.errcode==0){
          this.$message({
            message: '清空成功',
            type: 'success'
          });
        }else{
          this.$message.error(res.data.errmsg);
        }
      })
    },
    //新增采集
    addcaiji(){
      let fileobj = document.getElementById("files").files[0];
      // 创建form对象 
      let formobj = new FormData()
      formobj.append('file',fileobj);
      if(fileobj){
        axios({
          method: 'post',
          url:"http://zhuishu.zhiask.cn/upload",
          data:formobj,
        })
        .then(res=>{
          if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
          console.log(res);
          this.filename=res.data;
        })
      }
      this.caiji();
    },
    caiji(){
      let that=this
      setTimeout(function(){
        let temp = that.link.split(/[\n\r]/g);
        console.log(temp);
        let data={
          "action": "add_crawler",
          "data": {
            "id":that.taskmsg.id,
            "title":that.bookname,
            "author":that.zuozhe,
            "link":that.link,
            "filename":that.filename
          }
        }
        console.log(data)
        let token=sessionStorage.getItem("token");
              if(token==""||token==undefined){
        this.$router.push("/")
      }
        let url="http://zhuishu.zhiask.cn/api";
        axios({
          method: 'post',
          url: url,
          data: JSON.stringify(data),
          headers:{
            "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
            "Access-Token":token,             'cache-control': 'no-cache',
          },
          contentType: "text/html;charset=utf-8",
          dataType: "json",
        })
        .then(res=>{
          if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
          console.log(res.data);
          if(res.data.errcode==0){
            that.$message({
              message: '添加采集成功',
              type: 'success'
            });
          }else{
            that.$message.error(res.data.errmsg);
          }
        })
        },500)
    },
    submin(){
      if(this.time==0||this.time==""){
         this.$message.error('采集频率不能为空或者0');
        return;
      }
      let data={
        "action": "mod_crawler",
        "data": {
          "id":this.taskmsg.id,
          "name":this.taskmsg.name,
          "filter":this.taskmsg.filter,
          "interval":this.time*1000,
          "account":this.taskmsg.account
        }
      }
      console.log(data);
      let token=sessionStorage.getItem("token");
            if(token==""||token==undefined){
        this.$router.push("/")
      }
      let url="http://zhuishu.zhiask.cn/api";
      axios({
        method: 'post',
        url: url,
        data: JSON.stringify(data),
        headers:{
          "Content-Type": "application/json" ,//multipart/form-data;boundary=--xxxxxxx   application/json
        "Access-Token":token,             'cache-control': 'no-cache',
        },
        contentType: "text/html;charset=utf-8",
        dataType: "json",
      })
      .then(res=>{
        if(res.data.errcode==3){
          this.$message.error("登录过期，请重新登录")
          this.$router.push("/");
        }
        console.log(res.data);
        if(res.data.errcode==0){
          this.$message({
            message: '修改成功',
            type: 'success'
          });
          sessionStorage.setItem("taskmsg",JSON.stringify(data.data))
        }else{
          this.$message.error(res.data.errmsg);
        }
      })
    },
    //删除过滤，用户
    deleta(type,i){
      if(type==0){
        this.taskmsg.filter.splice(i,1);
      }else{
        this.taskmsg.account.splice(i,1);
      }
    },
    //添加过滤和用户
    addarr(type){
      if(type==0){
        console.log("asdf ");
        let arr1={
          pattern:this.before,
          replace:this.after
        }
         this.taskmsg.filter=this.taskmsg.filter.concat(arr1);
      }else{
        let arr2={
          user:this.uname,
          pass:this.upwd
        }
         this.taskmsg.account=this.taskmsg.account.concat(arr2);
      }
    }
    
  },
  created(){
    this.taskmsg=JSON.parse(sessionStorage.getItem("taskmsg"));
    console.log(this.taskmsg);
    this.time=this.taskmsg.interval/1000;
  }
}
</script>
<style>
  a,div,img,span,p,input,h1,h2,h3,h4,h5,h6,header,body,footer,ul,li{
  margin:0px;
  padding:0px;
}
.task-management{
  display:block;
  width: 97%;
  height: 100%;
  padding-left:32px;
}
.task-management .top1{
  width: 97%;
  padding:20px 0px 10px 0px;
  display: flex;
  justify-content: space-between;
  font-family: Microsoft Yahei;
  color: rgba(16, 16, 16, 1);
  border-bottom: 1px solid rgba(187, 187, 187, 1);
  margin-bottom: 13px;
}
.task-management .top1>div{
  display: flex;
  align-items: baseline
}
.task-management .top1 .left1 .tm{
  width: 100px;
}
.task-management .top1 .left1 .back{
  width: 80px;
  height: 25px;
  line-height: 25px;
  margin-left: 10px;
}
.task-management .top1 .task-list{
  width: 78px;
  height: 25px;
  border-radius: 4px;
  font-size: 12px;
  line-height: 27px;
  text-align: center;
  border: 1px solid rgba(187, 187, 187, 1);
  position: relative;
  top:6px;
  margin-left:10px;
  cursor: pointer;
}
.task-management .new-add{
  width: 97%;
  padding:0px 0px 0px 0px;
}
.task-management .new-add .name-zz{
  padding-left:63px;
  display: flex;
  margin-bottom: 22px;
}
.task-management .new-add .name-zz .book-name{
  margin-right: 41px;
}
.task-management .new-add .name-zz div span{
  width: 32px;
  height: 24px;
  color: rgba(16, 16, 16, 1);
  font-size: 16px;
  text-align: left;
  font-family: SourceHanSansSC-regular;
}
.task-management .new-add .name-zz div input{
  width: 189px;
  height: 27px;
  color: rgba(136, 136, 136, 1);
  font-size: 14px;
  text-align: left;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  margin-left: 12px;
  padding-left: 5px;
  border-radius: 5px;
  outline: none;
}
.task-management .new-add .add-link{
  display:flex;
  margin-bottom: 18px;
}
.task-management .new-add .add-link span{
  width: 96px;
  height: 24px;
  color: rgba(16, 16, 16, 1);
  font-size: 16px;
  font-family: SourceHanSansSC-regular;
  margin-right:16px;
}
.task-management .new-add .add-link textarea{
  width: 475px;
  height: 184px;
  line-height: 20px;
  color: rgba(136, 136, 136, 1);
  font-size: 14px;
  text-align: left;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  resize:none;
}
.task-management .new-add .add-more{
  margin-bottom: 19px;
}
.task-management .new-add .add-more input{
  margin-left:12px;
  cursor: pointer;
}
.task-management .new-add .add-more>div{
  padding-left:113px;
  margin-top:20px;
}
.task-management .new-add .add-more>div .btn{
  display: inline-block;
  width: 97px;
  height: 27px;
  line-height: 27px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  font-size: 13px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  margin-right: 14px;
  cursor: pointer;
}
.task-management .set{
  width: 97%;
  padding:0px 0px 0px 0px;
  margin-top:24px;
}
.task-management .title{
  height: 40px;
  line-height: 40px;
  padding-left:10px;
  color: rgba(16, 16, 16, 1);
  font-size: 16px;
  font-family: SourceHanSansSC-bold;
  background-color: rgba(244, 244, 244, 1);
  margin-bottom: 16px;
  font-weight: bold;
}
.task-management .set .plan-name{
  font-size: 16px;
  font-family: SourceHanSansSC-regular;
  margin-bottom: 27px;
}
.task-management .set .plan-name span{
  width: 112px;
  height: 24px;
  color: rgba(16, 16, 16, 1);
}
.task-management .set .plan-name input{
  width: 334px;
  height: 34px;
  color: rgba(136, 136, 136, 1);
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  padding-left: 5px;
  border-radius: 5px;
  font-size: 14px;
  outline: none;
}
.task-management .set .set-zi,.task-management .set .user{
  display: flex;
}
.task-management .set .set-zi div,.task-management .set .user div{
  margin-right: 53px;
}
.task-management .set .set-zi span,.task-management .set .user span{
  width: 96px;
  height: 24px;
  color: rgba(16, 16, 16, 1);
  font-size: 16px;
  text-align: left;
  font-family: SourceHanSansSC-regular;
}
.task-management .set .set-zi input,.task-management .set .user input{
  width: 189px;
  height: 34px;
  color: rgba(136, 136, 136, 1);
  font-size: 14px;
  text-align: left;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  padding-left: 5px;
  margin-left: 12px;
  border-radius:5px;
  outline: none;
}
.task-management .set  .set-result,.task-management .set  .user-result{
  margin-top:20px;
}
.task-management .set  .set-result li,.task-management .set  .user-result li{
  list-style: none;
  padding:8px 0px;
  width: 468px;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
  display:flex;
  position: relative;
}
.task-management .set  .set-result li span,.task-management .set  .user-result span{
  margin-right: 10px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  font-family: SourceHanSansSC-regular;
}
.task-management .set  .set-result li img,.task-management .set  .user-result li img{
  position: absolute;
  top:8px;
  right:0px;
  width: 15px;
  height: 15px;
  cursor: pointer;
}
.task-management .set  .set-result li .icon-chahao,.task-management .set  .user-result li .icon-chahao{
  font-size: 20px;
  cursor: pointer;
}
.task-management .set .collection-times{
  margin:29px 0px 20px 0px;
  padding-bottom: 25px;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
}
.task-management .set .collection-times span{
  width: 64px;
  height: 24px;
  color: rgba(16, 16, 16, 1);
  font-size: 16px;
  font-family: SourceHanSansSC-regular;
}
.task-management .set .collection-times input{
  width: 117px;
  height: 34px;
  color: rgba(136, 136, 136, 1);
  font-size: 14px;
  text-align: left;
  font-family: Roboto;
  border: 1px solid rgba(187, 187, 187, 1);
  margin:0px 11px 0px 26px;
  padding-left: 10px;
  outline: none;
  border-radius: 5px;
}
.task-management .set .set-zi .btn ,.task-management .set .user .btn{
  width: 93px;
  height: 34px;
  border-radius: 6px;
  line-height: 34px;
  color: rgba(16, 16, 16, 1);
  font-size: 14px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  margin-left: -37px;
  cursor: pointer;
}
.task-management  .hr{
  width: 97%;
  border-bottom: 1px solid rgba(187, 187, 187, 1);
  margin:30px 0px;
}
.task-management .submit{
  text-align: center;
  margin-bottom:20px;
}
.task-management .submit .btn{
  display: inline-block;
  width: 155px;
  height: 36px;
  line-height: 36px;
  border-radius: 4px;
  color: rgba(16, 16, 16, 1);
  font-size: 16px;
  text-align: center;
  font-family: Microsoft Yahei;
  border: 1px solid rgba(187, 187, 187, 1);
  margin:0px 11px;
  cursor: pointer;
}
</style>


